    <img src="static/image/202007243.png">
    <p>canvas</p>
    <img src="static/image/202007244.png">
    <img src="static/image/202007245.png">
  </section>
</template>

<script>
import HPopover from './hPopover'
export default {
  name: 'hPoppverWindow',
  components: {HPopover}
}
</script>



