const hLoading = {
  install (Vue) {
    // transition
    // rendervnode
    const Mask = Vue.extend({
      render (createElement, hack) {
        let vLoading = createElement('div',
          {key: 'loading', attrs: {class: 'h-loading-shade loading'}},
          [createElement('img', {attrs: {src: require('static/image/loading.gif')}})])
        let vEmpty = createElement('div',
          {key: 'empty', attrs: {class: 'h-loading-shade'}},
          [createElement('img', {attrs: {src: require('static/image/empty.png')}})])
        return createElement('transition', {
          props: {name: 'h-loading-fade', mode: 'out-in'},
          on: {afterLeave: this.leave}
        }, [this.loading ? vLoading
          : (this.empty ? vEmpty : null)])
      },
      data () {
        return {
          loading: false, // 
          empty: false // 
        }
      },
      methods: {
        // removeClass
        leave () {
          (!this.loading && !this.empty) && this.$emit('removeClass')
        }
      }
    })
    const editClass = (type, el) => {
      el.classList.remove('h-loading-relative')
      if (type) {
        el.classList.add('h-loading-relative')
      }
    }
    //  v-h-loading
    Vue.directive('h-loading', {
      bind (el, binding) {
        // data
        const mask = new Mask({
          el: document.createElement('div'),
          data: {
            loading: binding.value.loading,
            empty: binding.value.empty
          }
        })
        el.instance = mask // el
        el.mask = mask.$el // domel
        mask.$on('removeClass', _ => {
          editClass(false, el)
        })
        editClass(binding.value.loading || binding.value.empty, el)// elclassclass 
        el.appendChild(mask.$el) // eldom
      },
      // data
      update (el, binding) {
        if (!binding.value.empty && !binding.value.loading) {
          el.instance.loading = binding.value.loading
          el.instance.empty = binding.value.empty
        } else {
          editClass(true, el)
          el.instance.loading = binding.value.loading
          el.instance.empty = binding.value.empty
        }
      },
      // domnull
      unbind (el, binding) {
        if (el.instance) {
          editClass(binding.value.loading || binding.value.empty, el)
          el.mask && el.mask.remove()
          el.mask = null
          el.instance && el.instance.$destroy()
          el.instance = null
        }
      }
    })
  }
}
export default hLoading



