<template>
    <div>
      <h4></h4>
      <p></p>
      <h4></h4>
      <p>mypromisethen catch finallythencatch
      </p>
      <h4></h4>
      <p>promisecatchthen</p>
    </div>
</template>

<script>
import {MyPromise} from './myPromise'
export default {
  name: 'myPromiseWindow',
  mounted () {
    new MyPromise((resolve, reject) => {
      setTimeout(() => {
        reject('bye')
      }, 2000)
    }).then(r => console.log('then1', r))
      .then(r => console.log('then2', r))
      .catch(e => console.log('catch1', e))
      .then(r => console.log('then3', r))
      .catch(e => console.log('catch2', e))
  }
}
</script>



